.Sidebar {
  -ms-flex: 1 1 30%;
      flex: 1 1 30%;
  display: none;
  -ms-flex-direction: column;
      flex-direction: column;
}

.SidebarHeader {
  margin: 0 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  height: 4em;
}

#sidebarToggle {
  display: none;
}

#sidebarToggle:checked ~ .Sidebar {
  display: -ms-flexbox;
  display: flex;
}

.ShownSidebar {
  display: -ms-flexbox;
  display: flex;
}

.HiddenSidebar {
  display: none;
}

@media (min-width:801px)  { 
  /* tablet, landscape iPad, lo-res laptops ands desktops */ 

  .Sidebar {
    display: -ms-flexbox;
    display: flex;
  }

  #sidebarToggle:checked ~ .Sidebar {
    display: none;
  }

}


