.GameThumbnail {
  -ms-flex: 1 1 16em;
      flex: 1 1 16em;
  margin: 0.3em;

  height: 14em;
}

.GameThumbnailContainer {

  position: relative;

  width: 100%;
  height: 100%;

  overflow: hidden;
}

.GameThumbnailPreload {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  color: white;
  font-size: 1em;
  font-weight: bold;
}

.GameThumbnailImageLoaded {

  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  opacity: 0;

  -webkit-transition: opacity 1500ms ease;

  -o-transition: opacity 1500ms ease;

  transition: opacity 1500ms ease;
}

.GameThumbnailVideo {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 50%;
  right: 50%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);

  opacity: 0;
  -webkit-transition: opacity 800ms ease;
  -o-transition: opacity 800ms ease;
  transition: opacity 800ms ease;
  
  height: 100%;
}

.show {
  opacity: 1;
}

.hide {
  opacity: 0;
}


